export default {
	data() {
		return {
			list:[{name:'测试',pic:'../../static/images/head1.png',id:0}]
		};
	},
	methods:{
		chooseIt(ind){
			let pages = getCurrentPages()
			let obj=this.list[ind]
			if(pages.length>1){
				let prePage = pages[pages.length - 2]
				if (prePage.$vm.changeData) {
				  wx.navigateBack({})
				  setTimeout(()=>{
					  prePage.$vm.setVal(obj.id,obj.name,'friend')
				  },100)
				}
			}
		}
	}
}
